import counter from '../../../components/counter.vue'
export default {
	data() {
		return {
			goodsList: [],
			theIndex: null, //控制滑动效果，当前滑动效果
			oldIndex: null, // 上一个左滑的下标
			isStop: false, // 禁止触发滑动
			selectedList: [], // 选中的商品列表
			isAllselected: false, //是否全选
			sumPrice: '0.00', // 合计
			footerbottom: 0
		};
	},
	components: {
		counter
	},
	onLoad() {
		//兼容H5下的底部菜单



	},
	onShow() {
		uni.getStorage({
			key: 'goodsList',
			success: (res => {
				for (let i = 0; i < res.data.length; i++) {
					res.data[i].selected = false
				}
				this.goodsList = res.data
				// 属性数据初始化
				this.selectedList = []
				this.isAllselected = false
				this.sumPrice = '0.00'
			})
		})
	},
	methods: {
		// 数量减
		sub(row) {
			if (row.number <= 1) {
				return
			}
			row.number--
			// 更新storage
			uni.getStorage({
				key: "goodsList",
				success: (res) => {
					uni.setStorageSync("goodsList", this.goodsList);
				}
			})
			this.sum()
		},
		// 数量加
		add(row) {
			row.number++
			// 更新storage
			uni.getStorage({
				key: "goodsList",
				success: (res) => {
					uni.setStorageSync("goodsList", this.goodsList);
				}
			})
			this.sum()
		},
		toGoods(e) {
			uni.navigateTo({
				url: '../../goods/goods?goodsInfo=' + JSON.stringify(e)
			});
		},

		//控制左滑删除效果-begin
		touchStart(index, event) {
			//多点触控 不触发
			if (event.touches.length > 1) {
				this.isStop = true;
				return;
			}
			this.oldIndex = this.theIndex;
			this.theIndex = null;
			//获取初始坐标
			this.initXY = [event.touches[0].pageX, event.touches[0].pageY];
		},
		touchMove(index, event) {
			//多点触控 不触发
			if (event.touches.length > 1) {
				this.isStop = true;
				return;
			}
			// 移动位置
			let moveX = event.touches[0].pageX - this.initXY[0];
			let moveY = event.touches[0].pageY - this.initXY[1];
			// 滑动停止或移动位置小 不触发
			if (this.isStop || Math.abs(moveX) < 5) {
				return;
			}
			if (Math.abs(moveY) > Math.abs(moveX)) {
				// 竖向滑动-不触发左滑效果
				this.isStop = true;
				return;
			}

			if (moveX < 0) { // 左滑
				this.theIndex = index;
				this.isStop = true;
			} else if (moveX > 0) { // 右滑
				if (this.theIndex != null && this.oldIndex == this.theIndex) {
					this.oldIndex = index;
					this.theIndex = null;
					this.isStop = true;
					setTimeout(() => {
						this.oldIndex = null;
					}, 150)
				}
			}
		},
		touchEnd(index, $event) {
			//结束禁止触发效果
			this.isStop = false;
		},
		//控制左滑删除效果-end
		deleteGoods(row) { // 单个删除
			// 更新 Storage
			uni.getStorage({
				key: 'goodsList',
				success: (res => {
					res.data.splice(res.data.indexOf(row), 1);
					uni.setStorageSync('goodsList', res.data)
				})
			})
			// 更新数组
			this.goodsList.splice(this.goodsList.indexOf(row), 1);
			this.selectedList.splice(this.selectedList.indexOf(row), 1);

			// 全部删除取消全选状态
			this.isAllselected = this.goodsList.length > 0 ? this.isAllselected : false;
			this.sum();
			this.oldIndex = null;
			this.theIndex = null;
		},
		deleteList() { // 全部删除，删除选中商品
			while (this.selectedList.length > 0) { // 循环删除
				this.deleteGoods(this.selectedList[0]);
			}
			// 更新初始的数据
			this.selectedList = [];
			this.isAllselected = false;
			this.sum();
		},
		selected(row) { // 单选
			row.selected = !row.selected;
			let i = this.selectedList.indexOf(row); // 数组中是否包含
			i > -1 ? this.selectedList.splice(i, 1) : this.selectedList.push(row); // 包含取消选中删除否则添加
			this.isAllselected = this.selectedList.length == this.goodsList.length; // 是否全选
			this.sum();
		},
		allSelect() { // 全选
			this.isAllselected = !this.isAllselected;
			let arr = [];
			this.goodsList.forEach((item, i) => {
				item.selected = this.isAllselected;
				arr.push(item);
			})
			this.selectedList = this.isAllselected ? arr : []; // 选中数组
			this.sum();
		},
		sum() { // 合计
			this.sumPrice = 0;
			this.goodsList.forEach((item, i) => {
				if (item.selected) {
					this.sumPrice = this.sumPrice + (item.number * item.price);
				}
			})
			this.sumPrice = this.sumPrice.toFixed(2); // 四舍五入为指定小数位数的数字
		},
		toConfirmation() { // 结算
			if (this.selectedList.length < 1) {
				uni.showToast({
					title: '请选择商品结算',
					icon: 'none'
				});
				return;
			}
			uni.setStorage({
				key: 'confirmList',
				data: this.selectedList,
				success: () => {
					uni.navigateTo({
						url: '../../order/confirm'
					})
				}
			})
		}
	}
}
